import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import store from '../store';
import { Transition } from 'react-transition-group';
import * as Analytics from '../lib/analytics';
// import Data from '../data';
// import Svg from '../svg';

const defaultStyle = {
  transition: `opacity 200ms linear`,
  opacity: 0,
};

const transitionStyles = {
  entered:  { opacity: 1 },
  exited:   { opacity: 0 },
};

class Enter extends Component {
  constructor() {
    super();

    this.start = this.start.bind(this);
  }

  componentDidMount() {
    this.setState({
      didMount: true,
    });
  }

  start() {
    Analytics.sendEvent('Start');

    store.dispatch({
      type: 'TEST_STATUS',
      status: 'START',
    });
  }

  render(props, state) {
    return (
      <Transition in={state.didMount} timeout={100}>
        { state => (
          <div style={{ ...defaultStyle, ...transitionStyles[state] }} className="pochtoy-enter">
            <div className="pochtoy-enter__title">     ?</div>
            <div className="pochtoy-enter__text">
              <p>    ,           <span style="color: #FF3939; font-weight: 700;">9 999</span>.  ,            .</p>
              <p> ,         .   ,     .</p>
            </div>
            <div className="pochtoy-enter__btn">
              <button className="pochtoy-btn" onClick={this.start}></button>
            </div>
          </div>
        ) }
      </Transition>
    );
  }
}

const mapStateToProps = function(store) {
  return {
    test: store.testState
  };
};

export default connect(mapStateToProps)(Enter);